.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #ffafbd, #ffc3a0); /* Gradient nền đẹp mắt */
  padding: 20px;
}

.App-header {
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 600px; /* Giới hạn chiều rộng */
}

h1 {
  color: #ff6f61;
  font-family: 'Arial', sans-serif;
  font-size: 2.5em;
  margin-bottom: 20px;
}

.image-container {
  margin: 20px 0;
}

.date-image {
  width: 50%; /* Giảm xuống 50% chiều rộng của container */
  max-width: 300px; /* Giới hạn kích thước tối đa nhỏ hơn */
  height: auto; /* Giữ tỉ lệ ảnh */
  border-radius: 10px; /* Bo góc ảnh */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Thêm bóng */
  object-fit: cover; /* Đảm bảo ảnh không bị méo */
}

.greeting {
  font-size: 1.4em;
  color: #333;
  margin: 10px 0;
}

.invitation {
  font-size: 1.3em;
  color: #555;
  line-height: 1.5;
  margin: 15px 0;
}

.signature {
  font-size: 1.2em;
  color: #ff6f61;
  font-style: italic;
}

/* Responsive design */
@media (max-width: 600px) {
  .App-header {
    padding: 20px;
  }

  h1 {
    font-size: 2em;
  }

  .date-image {
    width: 70%; /* Tăng kích thước ảnh trên mobile để vẫn dễ nhìn */
  }
}